//       "@all join the kickoff meeting. We will discuss project goals, timelines, and team assignments. Your participation is crucial. Meeting at: 06 Aug, 2024  12:30 Meeting Link: https://meet.google.com/kbb-xzgf-ssg",
//   },
// ];

export default function NoticeBoard() {
  const [isNoticeModal, setisNoticeModal] = useState(false);
  const [isSortedModal, setSortedModal] = useState(false);
  const [selectedOption, setSelectedOption] = useState("Sort by");
  const [selectedOptionMessage, setSelectedOptionMessage] = useState("All messages");
  const [isDropMessage, setisDropMessage] = useState(false);
  const [isDrop, setisDrop] = useState(false);
  const [isEditModal, setisEditModal] = useState(false);
  const [Active, setActive] = useState<string>("By original post date")
  const [messageOptions, setMessageOptions] = useState([
    "All messages",
    "Announcement",
    "FYI",
    "Heartbeat",
    "Pitch",
    "Question",
  ]);

  const {id} = useParams();
  const [notesData, setNotesData] = useState([]);

  useEffect(() => {
    const fetchNotes = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/noticeboard/latest`,
          {
            params:{
              projectId: id
            },
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        console.log(response.data);
        setNoticeBoardData([...response.data]);
     
      } catch (error) {
        console.error(error);
      }
    };

    fetchNotes();
  }, []);


  const [NoticeBoardData, setNoticeBoardData]= useState<noticeBoard[]>([])
  useEffect(() => {
    // console.log(NoticeBoardData, "hlo");
   
  }, [setNoticeBoardData]);
  const [isnoticeDetail, setisnoticeDetail] = useState(false);

  const handleClick = () => {
    setisNoticeModal(true);
  };

  const handleClickOptions = () => {
    setisDrop((prev) => !prev);
  };


  const options = [
    { value: "Sort by", label: "Sort by", icon: <ChartBarDecreasing className="w-4 h-4" /> },
    { value: "Edit", label: "Edit categories", icon: <Pencil className="w-4 h-4" /> },
  ];

  const handleOptionClick = (value: string) => {
    if (value === "Sort by") {
      setSortedModal(true);
      setisEditModal(false);
    } else {
      setSortedModal(false);
      setisEditModal(true);
    }
    setSelectedOption(value);
  };

  const handleMessageOptionClick = (option: string) => {
    setSelectedOptionMessage(option);
    setisDropMessage(false);
  };

  const handleUpdateMessageOptions = (updatedOptions: string[]) => {
    setMessageOptions(updatedOptions);
  };

  const sortedNotesData = [...NoticeBoardData].sort((a, b) => {
    return new Date(b.postedOn).getTime() - new Date(a.postedOn).getTime();
});

const sortedAlphabetically = [...NoticeBoardData].sort((a, b) =>
  a.title.localeCompare(b.title)
);

  return (
    <>
    {isnoticeDetail ? (
      <NoticeDetail isOpenDetail={isnoticeDetail} onClose={() => setisnoticeDetail(false)} />
    ) : (
      <>
        {isSortedModal && (
          <SortedModal
            isOpen={isSortedModal}
            onClose={() => setSortedModal(false)}
            Active={Active}
            setActive={setActive}
          />
        )}
        {isEditModal && (
          <EditModal
            isOpen={isEditModal}
            onClose={() => setisEditModal(false)}
            messageOption={messageOptions}
            onUpdateMessageOption={handleUpdateMessageOptions}
          />
        )}
        {isNoticeModal && (
          <NoticeBoardModal
            isOpen={isNoticeModal}
            onClose={() => setisNoticeModal(false)}
            messageOptions= {messageOptions}
            setMessagesOptions= {setMessageOptions}
          />
        )}
        {NoticeBoardData.length >= 0 ? (
          <div className="min-h-screen bg-[#F8F7FA]">
            <div className="pl-6 pr-6 pb-0">
              <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
                <div className="flex items-center space-x-2">
                  <Link href="/main/dashboard" className="hover:text-gray-900">
                    Dashboard
                  </Link>
                  <span className="text-gray-400">›</span>
                  <Link href="/projects" className="hover:text-gray-900">
                    Project Atlas 
                  </Link>
                  <span className="text-gray-400">›</span>
                  <span className="text-black">Notice Board</span>
                </div>
              </nav>
              <div className="flex justify-between">
                <div className="flex items-center space-x-2">
                  <button
                    className="flex flex-row justify-center items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg mt-4"
                    onClick={handleClick}
                  >
                    <Plus className="mr-2" /> New Message
                  </button>
                  <input
                    type="text"
                    placeholder="Search message"
                    className="mx-3 mt-4 px-6 py-2 border border-gray-200 rounded-lg text-sm"
                  />
                </div>
                <div className="flex items-center space-x-2 ">
                  <div className="relative">
                    <button
                      className="flex px-6 py-2 text-sm border border-gray-200 rounded-lg bg-white"
                      onClick={() => setisDropMessage((prev) => !prev)}
                    >
                      
                      {selectedOptionMessage || "All messages"}
                      <ChevronDown className="w-4 h-4 ml-4"/>
                    </button>
                    {isDropMessage && (
                      <div className="absolute right-0 mt-2 w-44 bg-white border rounded-lg shadow-lg">
                        {messageOptions.map((option) => (
                          <div
                            key={option}
                            className="px-4 py-2 text-sm hover:bg-[#565DBD] hover:text-[white] cursor-pointer"
                            onClick={() => handleMessageOptionClick(option)}
                          >
                            {option}
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
  
                  <div className="relative">
                    <button
                      className="w-auto bg-white px-2 py-2 rounded-lg flex items-center gap-2 border z-0"
                      onClick={handleClickOptions}
                    >
                      <Image
                        src={menu}
                        alt="menu"
                        width={18}
                        height={18}
                        className="mr-2"
                      />
                      {selectedOption || ""}
                    </button>
                    {isDrop && (
                      <div className="absolute right-0 mt-2 w-44 bg-white border rounded-lg shadow-lg">
                        {options.map((option) => (
                          <div
                            key={option.value}
                            className="flex gap-2 px-4 py-2 text-sm hover:bg-[#565DBD] hover:text-[white] cursor-pointer"
                            onClick={() => handleOptionClick(option.value)}
                          >
                            {option.icon}
                            {option.label}
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
  
            <div className="bg-white p-6 m-7 rounded-xl">
              {NoticeBoardData.length > 0 ? (
                Active === "By original post date" ? (
                  NoticeBoardData.map((note) => (
                    <div
                      key={note._id}
                      className="border-b-2 border-gray-300 p-4 mb-4 hover:cursor-pointer"
                      onClick={() => setisnoticeDetail(true)}
                    >
                      <div className="flex items-center gap-3 mb-2">
                        <Image
                          alt="profile"
                          src={avtar}
                          width={40}
                          height={40}
                          className="rounded-full"
                        />
                        <div>
                          <div className="text-gray-600">
                            {note.postedBy.fullName}
                            Published on:{" "}
                            <span className="text-gray-700">{note.postedOn.split("T")[0]}</span>
                          </div>
                        </div>
                      </div>
                      <h2 className="ml-12 text-lg font-semibold text-gray-800 mb-2">
                        {note.title}
                      </h2>
                      <p className="ml-12 text-sm text-gray-600">{note.description}</p>
                    </div>
                  ))
                ) : Active === "By latest comment" ?(
                   sortedNotesData.map((note) => (
                    <div
                      key={note._id}
                      className="border-b-2 border-gray-300 p-4 mb-4 hover:cursor-pointer"
                      onClick={() => setisnoticeDetail(true)}
                    >
                      <div className="flex items-center gap-3 mb-2">
                        <Image
                          alt="profile"
                          src="api/placeholder"
                          width={40}
                          height={40}
                          className="rounded-full"
                        />
                        <div>
                          <div className="text-gray-600">
                            {note.postedBy} 
                            Published on:{" "}
                           <span className="text-gray-700">{note.postedOn.split("T")[0]}</span>
                          </div>
                        </div>
                      </div>
                      <h2 className="ml-12 text-lg font-semibold text-gray-800 mb-2">
                        {note.title}
                      </h2>
                      <p className="ml-12 text-sm text-gray-600">{note.description}</p>
                    </div>
                    ))
                ) : (
                  sortedAlphabetically.map((note) => (
                    <div
                      key={note._id}
                      className="border-b-2 border-gray-300 p-4 mb-4 hover:cursor-pointer"
                      onClick={() => setisnoticeDetail(true)}
                    >
                      <div className="flex items-center gap-3 mb-2">
                        <Image
                          alt="profile"
                          src="api/placeholder"
                          width={40}
                          height={40}
                          className="rounded-full"
                        />
                        <div>
                          <div className="text-gray-600">
                            {note.postedBy} 
                             Published on:{" "}
                            <span className="text-gray-700">{note.postedOn.split("T")[0]}</span>
                          </div>
                        </div>
                      </div>
                      <h2 className="ml-12 text-lg font-semibold text-gray-800 mb-2">
                        {note.title}
                      </h2>
                      <p className="ml-12 text-sm text-gray-600">{note.description}</p>
                    </div>
                  ))
                )
              ) : (
                <div className="flex flex-col items-center justify-center py-8">
                  <Image src={avtar} alt="emptyBoard" />
                  <div className="mt-5">
                    <button
                      onClick={handleClick}
                      className="flex flex-row justify-center items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg"
                    >
                      Post your first notice
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        ) : null}
      </>
    )}
  </>
  
  );
}